.App {
  text-align: center;
  background-color: rgba(194, 178, 128, 0.6);
  min-height: 100vh;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #77815c;
  background-image: url("../public/Banner-photo.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 10% 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.css-u84ohb {
  background-color: rgb(119, 129, 92) !important;
}

.MuiTab-textColorPrimary {
  color: white !important;
  font-family: 'Alegreya Sans SC', sans-serif !important;
}

.Mui-selected {
  border: 2px solid white !important;
  font-weight: bold !important;
}

.MuiTabs-indicator {
  color: white !important;
}

.App-link {
  color: #61dafb;
}

h1 {
  font-family: 'Eagle Lake', cursive !important;
}

h3 {
  font-family: 'Eagle Lake', cursive !important;
}

h4 {
  font-family: 'Alegreya Sans SC', sans-serif !important;
}

h5 {
  font-family: 'Eagle Lake', cursive !important;
}

.MuiTypography-h5 {
  font-family: 'Eagle Lake', cursive !important;
  font-size: 1rem !important;
}

.MuiTypography-h6 {
  font-family: 'Eagle Lake', cursive !important;
  padding: 1rem 2rem 0 2rem;
}

p {
  font-family: 'Alegreya Sans SC', sans-serif !important;
}

.homePanel {
  margin: 0 auto;
}

.storyPanel {
  text-align: left;
  margin: 0 auto;
}

.partyPanel {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  margin: 0 auto;
}

.groomPartyMember {
  margin-bottom: 2rem;
}

.bridePartyMember {
  margin-bottom: 2rem;
}

.airportsWrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.airportCard {
  margin-bottom: 2rem;
}

.travelCard {
  margin-bottom: 2rem;
}

.travelCardHeader > div > span {
  font-size: 1.5rem !important;
}

.lodgingsWrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.lodgingCard {
  margin-bottom: 2rem;
}

.photoPanel {
  margin: 0 20%;
}

.photoWrapper {
  max-width: 100%;
  border: 3px solid black;
}

.venmoIcon {
  background-image: url('../public/venmo-icon.png');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 2rem;
  width: 2rem;
  margin: 4rem auto;
}

.gimmeMoney {
  max-width: 80%;
  margin: 0 auto;
}

@media only screen and (max-width: 769px) {
  h1 {
    font-size: 2rem !important;
  }

  h3 {
    font-size: 1.5rem !important;
  }

  h4 {
    font-size: 1.25rem !important;
  }
  
  h5 {
    font-size: 1.25rem !important;
  }

  .photoPanel {
    margin: 0;
  }
}

@media only screen and (min-width: 768px) {
  .App-header {
    padding: 10% 0;
  }

  h1 {
    font-size: 3rem !important;
  }

  h3 {
    font-size: 2.5rem !important;
  }

  h4 {
    font-size: 2rem !important;
  }

  .MuiTypography-h5 {
    font-size: 2rem !important;
  }

  .tabs {
    width: fit-content;
    margin: 0 auto;
  }

  .homePanel {
    max-width: 75%;
  }

  .storyPanel {
    max-width: 50%;
  }

  .partyPanel {
    flex-direction: row;
    max-width: 75%;
  }

  .airportsWrapper {
    flex-direction: row;
  }

  .airportCard {
    max-width: 33%;
    margin-bottom: 0;
  }

  .lodgingsWrapper {
    flex-direction: row;
  }

  .lodgingCard {
    max-width: 40%;
    margin-bottom: 0;
  }

  .gimmeMoney {
    max-width: 50%;
    margin: 0 auto;
  }

}
